// rooms section

.rooms {
    &_list {
        &-item {
            .card {
                border-radius: 8px;
                padding: 20px;

                .title {
                    color: #fff;
                    margin-bottom: 15px;
                }

                .content {
                    margin-top: 20px;
                    padding-left: 13px;
                    border-left: 2px solid #fff;

                    .text {
                        margin-bottom: 20px;
                        @include textSm;

                        b {
                            font-weight: 700;
                        }

                        &:last-of-type {
                            margin-bottom: 0;
                        }
                    }
                }

                .btn {
                    margin-top: 30px;
                    width: 100%;
                }
            }
        }
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .rooms {
        &_list {
            &-item {
                .card {
                    padding: 30px;

                    .btn {
                        width: fit-content;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1199.98px) {
    .rooms {
        &_list {
            &-item {
                .card {
                    padding: 50px;
                }
            }
        }
    }
}