// player component

.video {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(35, 87, 132, 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 400000;
    visibility: hidden;
    transition: all .4s ease-in-out;


    &.visible {
        visibility: visible;
    }

    &_frame {
        position: relative;

        &-close {
            position: absolute;
            right: 0;
            top: -30px;
            font-size: 24px;
            color: #fff;
            cursor: pointer;
        }

        iframe {
            border: none;
            width: 100%;
            height: 100%;
        }
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .video {
        &_frame {
            height: 500px;
        }
    }
}

@media screen and (min-width: 991.98px) {
    .video {
        &_frame {
            height: 700px;
        }
    }
}